import React, { Component } from 'react';
import { render } from 'react-dom';
import { Card, WingBlank, WhiteSpace, List, InputItem, Popover, Picker, Modal, Icon, NavBar, Button, ActivityIndicator, Stepper } from 'antd-mobile';
import url from 'url';
import clone from 'clone';
import Cookies from "js-cookie";
import '../reset.less';
import './style.less';
import { getQueryString, getColorName, concatCard, getRedom } from '../util';
import $ from 'jquery';
const axios = require('axios');
const Item = Popover.Item;
let userInfo = {
    userid: getQueryString('uid'),
    nickname: 'huangwei',
    headimgurl: '/images/games/majiang/head.jpg'
};
if (process.env.NODE_ENV !== 'development') {
    const userInfoCookie = Cookies.get('wxUserInfo');
    if (!userInfoCookie) {
        location.href = '/auth?target=home';
    } else {
        console.log(JSON.parse(userInfoCookie));
        userInfo = JSON.parse(userInfoCookie);
    }
}

class LayOut extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isAllow: false,
            user: userInfo
        }
    }
    componentDidMount() {
        window.addEventListener('popstate', function () {
            history.pushState(null, null, document.URL);
        });
        // axios.post('/api/login', {
        //     openId: openId
        // }).then(function (response) {
        //     if (!response.data) {
        //         //
        //         axios.post('/api/reg', {
        //             openId: openId,
        //             username: uaerName
        //         }).then(function (response) {
        //             console.log(response);
        //         });
        //     } else {
        //         //
        //     }
        // }).catch(function (error) {
        //     console.log(error);
        // });
        window.setTimeout(() => {
            this.setState({
                isAllow: true
            })
        }, 1000);
    }
    render() {
        return this.state.isAllow ? <NewRoom user={this.state.user} /> : <div className="toast-example">
            <ActivityIndicator
                toast
                text="..."
                animating={true}
            />
        </div>
    }
}
class NewRoom extends Component {
    constructor(props) {
        super(props);

        this.state = {
            roomCard: 0,
            roomId: 0,
            visible: false,
            modal_visible: false,
            roomInfo_visible: false,
            modal_title: '',
            modal_details: '',
            rule: ['chengdu'],
            ruleName: '',
            mulriple: 1,
            colorType: [3],
            countdown: [20]
        }
        this.ruleData = [
            { value: 'chengdu', label: '' },
            { value: 'guangan', label: '' }
        ]
        this.checkinHandler = this.checkinHandler.bind(this);
    }
    checkinHandler() {
        axios.post('/api/checkin', {
            uid: this.props.user.userid,
            rule: this.state.rule[0],
            ruleName: this.state.ruleName,
            mulriple: this.state.mulriple,
            colorType: this.state.colorType[0],
            countdown: this.state.countdown[0]
        }).then((data) => {
            if (process.env.NODE_ENV === 'development') {
                this.setState({
                    roomInfo_visible: true,
                    roomId: data.data
                });
            } else {
                location.href = `http://www.fanstongs.com/room?roomId=${data.data}`;
            }
        });
    }
    render() {

        return <div className='flex-container'>
            <div className="sub-title">
                <img src='/images/games/majiang2/roomCheckin.png' />
            </div>

            <WingBlank size="lg">
                <WhiteSpace size="lg" />
                <Card>
                    <Card.Header
                        title=""
                        thumb='/images/games/majiang2/roomCard.png'
                        extra={<span>checkin</span>}
                    />
                    <Card.Body>
                        <List>
                            <List.Item>
                                {/* <InputItem
                                    value={this.state.roomCard}
                                    maxLength={2}
                                    type='number'
                                    placeholder="4"
                                    onChange={v => this.setState({ roomCard: v })}
                                >:</InputItem> */}
                                <span style={{ display: 'inline-block', width: '5.3rem', marginLeft: '1rem' }}></span>
                                <Stepper
                                    style={{ width: '7rem', marginLeft: '1rem' }}
                                    showNumber
                                    max={5}
                                    min={0}
                                    value={this.state.roomCard}
                                    onChange={v => this.setState({ roomCard: v })}
                                />
                                <div className='iconFloat'>
                                    <Icon type='ellipsis' onClick={() => {
                                        this.setState({
                                            modal_visible: true,
                                            modal_title: '',
                                            modal_details: '4'
                                        })
                                    }} />
                                </div>
                            </List.Item>
                            <List.Item>
                                <Picker data={this.ruleData} cols={1}
                                    value={this.state.rule}
                                    onChange={v => this.setState({
                                        rule: v,
                                        ruleName: this.ruleData.find(item => item.value === v[0]).label
                                    })}
                                    onOk={v => this.setState({
                                        rule: v,
                                        ruleName: this.ruleData.find(item => item.value === v[0]).label
                                    })}>
                                    <List.Item arrow="horizontal"></List.Item>
                                </Picker>
                                <div className='iconFloat'>
                                    <Icon type='ellipsis' onClick={() => {
                                        this.setState({
                                            modal_visible: true,
                                            modal_title: '',
                                            modal_details: ''
                                        })
                                    }} />
                                </div>
                            </List.Item>
                            <List.Item>
                                {/* <InputItem
                                    value={this.state.mulriple}
                                    type='number'
                                    placeholder=""
                                    onChange={v => this.setState({ mulriple: v })}
                                >:</InputItem> */}
                                <span style={{ display: 'inline-block', width: '5.3rem', marginLeft: '1rem' }}></span>
                                <Stepper
                                    style={{ width: '7rem', marginLeft: '1rem' }}
                                    showNumber
                                    max={10}
                                    min={1}
                                    value={this.state.mulriple}
                                    onChange={v => this.setState({ mulriple: v })}
                                />
                                <div className='iconFloat'>
                                    <Icon type='ellipsis' onClick={() => {
                                        this.setState({
                                            modal_visible: true,
                                            modal_title: '',
                                            modal_details: '1'
                                        })
                                    }} />
                                </div>
                            </List.Item>
                            <List.Item>
                                <Picker data={[
                                    { value: 3, label: '' },
                                    { value: 2, label: '' }
                                ]} cols={1}
                                    value={this.state.colorType}
                                    onChange={v => this.setState({ colorType: v })}
                                    onOk={v => this.setState({ colorType: v })}>
                                    <List.Item arrow="horizontal"></List.Item>
                                </Picker>
                                <div className='iconFloat'>
                                    <Icon type='ellipsis' onClick={() => {
                                        this.setState({
                                            modal_visible: true,
                                            modal_title: '',
                                            modal_details: ''
                                        })
                                    }} />
                                </div>
                            </List.Item>
                            <List.Item>
                                <Picker data={[
                                    { value: 10, label: '10S' }, { value: 20, label: '20S' },
                                    { value: 30, label: '30S' }, { value: 40, label: '40S' },
                                    { value: 50, label: '50S' }, { value: 60, label: '60S' }
                                ]} cols={1}
                                    value={this.state.countdown}
                                    onChange={v => this.setState({ countdown: v })}
                                    onOk={v => this.setState({ countdown: v })}>
                                    <List.Item arrow="horizontal">()</List.Item>
                                </Picker>
                                <div className='iconFloat'>
                                    <Icon type='ellipsis' onClick={() => {
                                        this.setState({
                                            modal_visible: true,
                                            modal_title: '',
                                            modal_details: ''
                                        })
                                    }} />
                                </div>
                            </List.Item>
                            <List.Item>
                                <Button type="primary" onClick={this.checkinHandler}></Button>
                            </List.Item>
                        </List>
                    </Card.Body>
                    <Card.Footer content="20" extra={<div>5263</div>} />
                </Card>
                <WhiteSpace size="lg" />
            </WingBlank>
            <Modal
                visible={this.state.modal_visible}
                transparent
                maskClosable={true}
                title=""
                animationType='slide-down'
                footer={[{ text: '', onPress: () => { this.setState({ modal_visible: false }) } }]}
            >
                <div style={{ textAlign: 'left' }}>{this.state.modal_details}</div>
            </Modal>
            <Modal
                visible={this.state.roomInfo_visible}
                transparent
                maskClosable={true}
                title=""
                animationType='slide-down'
                footer={[{ text: '', onPress: () => { } }]}
            >
                <div style={{ textAlign: 'left' }}>
                    <a href={`http://www.fanstongs.com/room?roomId=${this.state.roomId}`}></a>~~
                <div>http://fanstongs.com/room?roomId={this.state.roomId}</div>
                </div>
            </Modal>
        </div>
    }
}

render(
    <LayOut />,
    document.getElementById('layout')
)


